@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR&display=swap);
.categories-block{display:flex;padding:1rem;width:768px;margin:0 auto}@media screen and (max-width: 768px){.categories-block{width:100%;overflow-x:auto}}.category{font-size:1.125rem;cursor:pointer;white-space:pre;text-decoration:none;color:inherit;padding-bottom:0.25rem}.category:hover{color:#495057}.category+.category{margin-left:1rem}

.news-item-block{display:flex}.news-item-block .thumbnail{margin-right:1rem}.news-item-block .thumbnail img{display:block;width:160px;height:100px;object-fit:cover}.news-item-block .contents h2{margin:0}.news-item-block .contents h2 a{color:black}.news-item-block .contents p{margin:0;line-height:1.5;margin-top:0.5rem;white-space:normal}.news-item-block+.news-item-block{margin-top:3rem}

.news-list-block{box-sizing:border-box;padding-bottom:3rem;width:768px;margin:0 auto;margin-top:2rem}@media screen and (max-width: 768px){.news-list-block{width:100%;padding-left:1rem;padding-right:1rem}}

body {
  font-family: 'Noto Sans KR', sans-serif;
}

